body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.title {
    padding: 10px;
    text-align: center;
    background: green;
    color: white;
    font-size: 25px;
    font-weight: bold;
}
.page {
    padding-left: 10%;
    padding-right: 10%;
}

.basicTable {
    width: 100%;
}
td {
    border: 1px solid black;
  }

  ul.navigation li {
    display: inline;
    list-style-type: none;
    margin: 0;
  }
  ul.navigation {
    background-color: lightgreen;
    padding: 0;
  }
  ul.navigation li a {
    color: #FFF;
    font-weight: bold;
    text-decoration: none;
    padding: 20px;
    display: inline-block;
  }
  .content {
    background-color: #FFF;
  }

  .content h2 {
    padding: 0;
    margin: 0;
  }
  .content li {
    margin-bottom: 10px;
  }
.footer {
    padding: 5px;
    left: 0;
    bottom: 0;
    width: 100%;
    background-color: green;
    color: white;
    text-align: center;
    font-weight: bold;
}
